<template>
    <div class="footer">
      <ul>
        <router-link to = "/home" tag = "li">
          <span class="iconfont icon-yemian-copy1"></span>
          <p>首页</p>
        </router-link>
        <router-link to = "/mall" tag = "li">
          <span class="iconfont icon-shangcheng"></span>
          <p>商城</p>
        </router-link>
        <router-link v-if="!isLogin" to = "/user" tag = "li">
          <span class="iconfont icon-wode-"></span>
          <p>我的</p>
        </router-link>
        <router-link v-if="isLogin"  to = "/user/login" tag = "li">
          <span class="iconfont icon-wode-"></span>
          <p>我的</p>
        </router-link>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "Footer",
      data () {
          return {
            isLogin: true
          }
      },
      mounted() {
        if(this.$cookie.get('accessToken')){
          this.isLogin = true
        } else {
          this.isLogin = false
        }
        console.log(this.isLogin)
      }
    }
</script>

<style scoped  lang="scss" >
  .iconfont{
    font-size: 0.26rem;
    color: #BAC0CA;
  }
  p{
    color: #BAC0CA;
  }
.router-link-active .iconfont,.router-link-active p{
  color:#4E8DF4;
}
</style>



